.select {
  border-radius: 30px;
  border: 1px solid #e0e0e0;
  padding: 0.5rem 1rem;
  font-size: 12px;
  cursor: pointer;
  width: 100%;
  min-width: 200px;
  -moz-appearance: none; /* Firefox */
  -webkit-appearance: none; /* Safari and Chrome */
  appearance: none;
  background-image: linear-gradient(45deg, transparent 50%, var(--dark-blue) 50%),
    linear-gradient(135deg, var(--dark-blue) 50%, transparent 50%), radial-gradient(var(--light) 70%, transparent 72%);
  background-position: calc(100% - 15px) calc(1em + 2px), calc(100% - 10px) calc(1em + 2px), calc(100% - 0.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1.5em 1.5em;
  background-repeat: no-repeat;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.6);
}

.select:focus {
  outline: 0;
}
